.navi {
  height: 65px;
  border-bottom: 1px solid #e6e6e6;
  font-family: "Malgun Gothic", "Apple SD Gothic Neo", Dotum;
  dl {
    width: 1000px;
    margin: 0 auto;
    display: flex;
    dt {
      padding: 20px;
      font-size: 15px;
      font-weight: bold;
      color: #828282;
    }
    dd {
      padding: 20px;
      font-size: 13px;
      color: #828282;
    }
    .border {
      border: 1px solid red;
      padding: 10px 15px;
      color: red;
    }
  }
}
.container {
  height: 863px;
  .box {
    h1 {
      margin-top: 20px;
      margin-left: 450px;
    }
    .bar {
      border-left: 1px solid #b5b5b5;
      border-right: 1px solid #b5b5b5;
      height: 9px;
      font-size: 1px;
      margin-bottom: 2px;
      width: 52%;
      margin: 50px auto 0;
      p {
        border-bottom: 1px solid #b5b5b5;
        height: 4px;
        font-size: 1px;
      }
    }
    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
    table {
      width: 1000px;
      height: 208px;
      margin: 0 auto;
      border-color: grey;
      color: #807f83;
      border-top: 1px solid #d1d1d1;
      border-left: 1px solid #d1d1d1;
      th {
        text-align: left;
        padding: 15px;
        border-bottom: 1px solid #d1d1d1;
        border-right: 1px solid #d1d1d1;
        font-size: 12px;
      }
      td {
        padding: 15px;
        font-size: 12px;
        border-bottom: 1px solid #d1d1d1;
        border-right: 1px solid #d1d1d1;
        #userId {
          margin-right: 10px;
          height: 35px;
        }
        #password {
          height: 35px;
        }
        #email {
          height: 35px;
          width: 250px;
        }
        #tel {
          height: 35px;
        }
        span {
          margin-left: 10px;
        }
      }
      .btn {
        a {
          width: 94px;
          height: 38px;
          display: inline;
        }
        .sns_btn {
          width: 184px;
          height: 40px;
          display: inline;
          margin-left: 250px;
          img {
            width: 184px;
            height: 40px;
            margin-left: 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
