.mySwiper {
  height: 1060px;
  border-bottom: 1px solid #000;
  .timeline {
    height: 900px;
    .bg {
      width: 70%;
      height: 900px;
      position: absolute;
      top: 5%;
      right: -3%;
      cursor: pointer;
    }
    .bg1 {
      background: url(../../images/timeline1.jpg) no-repeat;
    }
    .bg2 {
      background: url(../../images/timeline2.jpg) no-repeat;
    }
    .bg3 {
      background: url(../../images/timeline3.jpg) no-repeat;
    }
    .bg4 {
      background: url(../../images/timeline4.jpg) no-repeat;
    }
    .bg5 {
      background: url(../../images/timeline5.jpg) no-repeat;
    }
    .bg6 {
      background: url(../../images/timeline6.jpg) no-repeat;
    }
    .bg7 {
      background: url(../../images/timeline7.jpg) no-repeat;
    }
    .txt {
      font-family: "Centra No2", "Noto Sans KR";
      margin-top: 140px;
      margin-left: 100px;
      h2 {
        font-size: 58px;
        font-weight: 400;
        margin-bottom: 30px;
        line-height: 90px;
      }
      h3 {
        font-size: 80px;
        font-weight: bold;
        margin-bottom: 30px;
      }
      p {
        font-size: 14px;
        font-weight: bold;
        color: #999;
        float: left;
        padding-top: 20px;
        padding-left: 10px;
        &:hover {
          color: #000;
        }
      }
      img {
        width: 100px;
        height: 100px;
        float: left;
        cursor: pointer;
        border-radius: 50%;
      }
      span {
        background: url(../../images/outlinkPlay.svg) no-repeat;
        width: 28px;
        height: 28px;
        display: inline-block;
        position: absolute;
        left: 170px;
      }
    }
  }
}

.swiper-button-prev {
  left: 6%;
  top: 75%;
  color: #000;
}
.swiper-button-next {
  left: 15%;
  top: 75%;
  color: #000;
}
